@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap');
* {
  margin: 0;
  padding: 0 ;
  box-sizing: border-box;
}

.card {
  background: #1f1f1fe5;
  color: white;
  padding: 2em;
  border-radius: 30px;
  width: 100%;
  max-width: 600px;
  margin: 1em;
}

.App{
  background-size: cover;
  background-position: bottom;
  transition: 0.4s ease-out;
}

.App.warm {
}

.flex{
  display: flex;
  align-items: center;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url('https://source.unsplash.com/1920x1080/?weather');
  font-size: 120%;
  overflow: hidden;
  font-family: 'Source Sans Pro', sans-serif;

}

.search-box{
  width: 100%;
}

.search-box .search-bar {
  width: 100%;
  padding: 24px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: #7c7c7c5b;
  border-radius: 25px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: white;
  font-size: 20px;
  transition: 0.4s ease;
}

.search-box .search-bar:focus{
  background-color: rgba(255, 255, 255, 0.274);

}

.location-box .location{
  margin-top: 10px;
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  margin-top: 5px;
  color: #FFF;
  font-size: 20px ;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box{
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding:  15px 25px;

  color: #FFF;

  font-size: 50px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  margin-bottom: 15px;
  text-transform: capitalize;
  color: white;
  font-size: 35px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5)
}

.title{

  align-content: center;
  text-align: center;
  font-size: 140%;
  display: block;
  margin-bottom: 15px;
}
